*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
h1{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.app{
    background-image: url('./assets/gettyimages-107191741-612x612.jpg');
    background-size: cover;
    background-position: bottom;
    transition: 0.4 ease;
}

.app.warm{ 
    background-image: url(./assets/img2.jpeg);
}


main{
    min-height: 100vh;
    /* border: 1px solid red; */
    padding: 25px;
    /* background-color: cyan; */
   
}

.search-box{
    width: 100%;
    margin: 0 0 75px;
}
.search-bar{
    display: block;
    width: 100%;
    padding: 15px;
    appearance: none;
    background: none;
    outline: none;
    background-color:rgb(255, 255, 255);
    border-radius: 0 0 10px 10px;
    box-shadow: 0px 5px rgba(58, 41, 41, 0.2);
    /* color: #313131; */
    font-size: 25px;
    transition: 0.4s ease;
}

.search-box .search-bar:hover{
    background-color: rgba(161, 194, 202, 0.75);
}

.location-box{
    color: white;
    /* border: 1px solid blue; */
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 30px;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}
.date{
    font-style: italic;
    font-weight: 300;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}


.weather-box{
    /* border: 1px solid blue; */
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 20px;
    margin-top: 2rem;
}
.temp{
    font-size: 102px;
    font-weight: 900;
    padding: 15px 25px;
    margin: 30px auto;
    background-color: rgba(255,255,255,0.2);
    border-radius: 16px;
    text-shadow: 3px 6px rgba(50,50,70,0.5);
    box-shadow: 3px 6px rgba(0,0,0,0.2);
    display: flex;
    color: #fff;
    justify-content: center;
    /* border: 1px solid blue;  */
}
.weather{
    font-size: 30px;
    /* margin: 15px; */
    color: #fff;
    font-weight: 600;
    text-shadow: 3px 6px rgba(50,50,70,0.5);
    
}